import React, {useState} from 'react'
import selfy from '../img/stojan1600.jpg';
import { motion } from "framer-motion"

export default function NavLeftContainer({ pages, language, isLanguageTrue }) {

    const [show, setShow] = useState(false);

    const buttonClicked = () => {
        const getEle = document.getElementById("navHamburger");
        if (!show) {
            getEle.className += " show";
        } else {
            getEle.className = "navbar";
        }
        setShow(prevState => !prevState);
    }

    const menuItemClicked = () => {
        const getEle = document.getElementById("navHamburger");
        getEle.className = "navbar";
        setShow(prevState => !prevState);
    }

    return (
        <React.Fragment>
            <div className="nav-top" >
                <div className="nav-name">
                    <p>Stojan Zajc</p>
                </div>
                <div id="hamburger" className="social-icons-top" onClick={buttonClicked}>
                    <a className="soc-img-top" >
                        <i className="fas fa-bars"></i>
                    </a>
                </div>
            </div>
            {
            // <div className="language-button">
            //     <button onClick={language} >{isLanguageTrue ? "EN" : "SLO"}</button>
            // </div>
            }
            <motion.div className="inner-nav"
                initial={{opacity: 0, y: -60}}
                animate={{opacity: 1, y: 0}}
                transition={{duration: 1}}>
                <img className="image-nav" src={selfy} alt="Photography" />
            </motion.div>
            <nav className="navbar" id="navHamburger">
                <ul className="nav flex-sm-column">
                    {
                        pages.map((page, i) => (
                            <motion.li className="nav-item" key={page.text}
                                initial={{x: -60, opacity: 0}}
                                animate={{x: 0, opacity: 1}}
                                transition={{duration: 0.5, delay: 0.5*i}}>
                                <a className="nav-link" 
                                    id={page.id} 
                                    href={"#" + page.textHref} 
                                    onClick={menuItemClicked} >{page.text}</a>
                            </motion.li>
                        ))
                    }
                </ul>
            </nav>
        </React.Fragment>
    )
}
