import React, { useState } from 'react'
import NavLeftContainer from '../subPages/NavLeftContainer';
import RightContainerAbout from '../subPages/RightContainerAbout';
import RightContainerEducation from '../subPages/RightContainerEducation';
import RightContainerSkills from '../subPages/RightContainerSkills';
import RightContainerInterests from '../subPages/RightContainerInterests';
import RightContainerExpirience from '../subPages/RightContainerExpirience';
//import NavLeftContainerEN from '../subPages/NavLeftContainer';
import RightContainerAboutEN from '../subPages/RightContainerAboutEN';
import RightContainerEducationEN from '../subPages/RightContainerEducationEN';
import RightContainerSkillsEN from '../subPages/RightContainerSkillsEN';
import RightContainerInterestsEN from '../subPages/RightContainerInterestsEN';
import RightContainerExpirienceEN from '../subPages/RightContainerExpirienceEN';

import './home.css';



export default function Home() {

    const [language, setLanguage] = useState(true)

    
    const pages = [
        {   
            textHref : "about",
            text: language ? "O meni" : "About me",
            id : "ab"},
        {   
            textHref : "expirience",
            text : language ? "izkušnje" : "Expirience",
            id : "ex"  },
        {   
            textHref : "education",
            text : language ? "izobrazba" : "Education",
            id : "ed"  },
        {   
            textHref : "skills/projects",
            text : language ? "spretnosti" : "Skills",
            id : "sk"  },
        {   
            textHref : "interests",
            text : language ? "zanimanja" : "Interests",
            id : "in"  },
    ];
    let d = new Date();
    let n = d.getFullYear();

    const languageSwap = () => {
        setLanguage(prevState => !prevState);
    }

    return (
        <div className="Container">
            <div className="Left-container">
                <NavLeftContainer pages={pages} language={languageSwap} isLanguageTrue={language}/>
            </div>
            <div className="Right-container">
                {
                    language ?
                    <React.Fragment>
                        <RightContainerAbout />
                        <hr className="bottom-line"></hr>
                        
                        <RightContainerExpirience />
                        <hr className="bottom-line"></hr>
                        <RightContainerEducation />
                        <hr className="bottom-line"></hr>
                        
                        <RightContainerSkills />
                        <hr className="bottom-line"></hr>
                        <RightContainerInterests />
                    </React.Fragment> :
                    <React.Fragment>
                        <RightContainerAboutEN />
                        <hr className="bottom-line"></hr>
                        <RightContainerExpirienceEN />
                        <hr className="bottom-line"></hr>
                        <RightContainerEducationEN />
                        <hr className="bottom-line"></hr>
                        <RightContainerSkillsEN />
                        <hr className="bottom-line"></hr>
                        <RightContainerInterestsEN />
                    </React.Fragment>
                }
                
                <hr className="bottom-line"></hr>
                <div className="small">
                    <small>© SZ {n}. All Rights Reserved.</small>
                </div>
            </div>
        </div>
    )
}
 