import React from 'react'

export default function RightContainerExpirience() {
    return (
        <section className="section-expirience" id="expirience">
            <div className="inner-expirience" >
                <h2>EXPIRIENCE</h2>
                <div className="exp-set">
                    <div className="left-box">
                        <div className="exp-heading">
                            <h3 className="h3m">MEDICAL ASSISTANT IN MEDICAL OFFICE</h3>
                        </div>
                        <div className="sub-heading"><span className="name">@ </span>Grosek Pšeničnik Marjana - Ordinacija splošne medicine, Ljubljana (Slovenija)
                        </div>
                        <p>
                            I am employed as a medical assistant in a private medical office,
                            where in addition with constant contact with the people and the organization
                            of the daily work plan, I also take care of system administration of both 
                            hardware and software.
                        </p>
                    </div>
                    <div className="right-box">
                        <p>January 2015 - in progress</p>
                    </div>
                </div>
                <div className="exp-set">
                    <div className="left-box">
                        <div className="exp-heading">
                            <h3 className="h3m">Telecommunication consultant</h3>
                        </div>
                        <div className="sub-heading"><span className="name">@ </span>T2 d.o.o., Ljubljana (Slovenija)</div>
                            <p>
                                As a consultant on the technical support of the telecommunications provider T-2 
                                I received calls from existing and potential service subscribers, provide 
                                information on the operation of services, checked the possibilities of 
                                connecting services and advised on the order of additional services. I also 
                                helped with the services that made it easier for eliminating technical errors, 
                                editing settings on telecommunication devices, etc.
                            </p>
                        </div>
                    <div className="right-box">
                        <p>May 2013 - November 2014</p>
                    </div>
                </div>
            </div>
        </section>
    )
}
