import React from 'react'

export default function RightContainerInterests() {
    return (
        <section className="section-interests" id="interests">
            <div className="inner-interests" >
                <h2>INTERESTS</h2>
                <div className="exp-set">
                    <div className="left-box-interests w100">
                        <div className="exp-heading">
                            <div className="sub-heading bm10">OTHER INTERESTS</div>
                        </div>
                        <p>
                            In my free time I like to travel and get to know new places, play football, read books and 
                            train in photography. I have finished elementary music school in violin.
                        </p>
                    </div>
                </div>
            </div>
        </section>
    )
}
